<template>
  <div class="ui-comp-user-card card">
    <div class="card__container">
      <div class="card__wrapper user">
        <div class="user__reg-date">{{ date }}</div>
        <div class="user__photo">
          <img 
            :src="photo" @error="handleErr" alt="user photo">
        </div>
        <p class="user__name">{{ userCard.name }}</p>
        <div class="user__mid-wrapper">

          <p class="user__job">{{ userCard.position }}</p>
          <p 
            class="user__email rel"
            id="email"
            @mouseover="handleHover" 
            @mouseleave="handleHover"
          >
            {{ userCard.email }}
            <span 
              v-if="toolTip.isHover && toolTip.el === 'email'"
              :class="{ 'tooltip': toolTip }"
              :style="tipPosition"
            >
              {{ userCard.email }}
            </span>
          </p>
          <p class="user__phone">{{ phone }}</p>

        </div>
      </div>
    </div>
  </div>
</template>

<script>
// API
// :userCard="user array"

export default {
  name: 'ui-comp-user-card',
  props: { 
    userCard: { type: Object, default: () => {} },
  },
  data() {
    return{
      cont: 'some content',
      toolTip: { isHover: false, el: '', x: 0 },
      altImg: require('@/assets//images/photo-cover.svg'),
      isImgOk: true
    }
  },
  computed: { 
    tipPosition () { return { left: `${this.toolTip.x - 20}px` } },
    
    photo() { return this.isImgOk ? this.userCard.photo : this.altImg },

    phone() {
      let n = this.userCard ? this.userCard.phone : null
      let x = n && n.replace(/\D/g, '').match(/(\d{0,2})(\d{0,3})(\d{0,3})(\d{0,2})(\d{0,2})/)
      if (!x) return n
      let res = `+${x[1] ? x[1] + ' ' : ''}(${x[2] ? x[2] + ') ' : ''}${x[3]}${x[4] ? ` ${x[4]}` : ''}${x[5] ? ` ${x[5]}` : ''}`
      return res
    },
    date() {return new Date(this.userCard.registration_timestamp).toDateString() }
  },

  methods: {
    handleErr(ev) { 
      this.isImgOk = false
      // ev.target.src = this.altImg
    },
    handleHover(ev) {
      if (ev.target.id === '') return
      else {
        if (ev.type === 'mouseover') {
          this.toolTip.isHover = true
          this.toolTip.el = ev.target.id
          this.toolTip.x = ev.offsetX
        }
        if (ev.type === 'mouseleave') this.toolTip = {}
      }
    }
  },
}
</script>

<style lang="scss" scoped>
@import '@/styles/main.scss';
.ui-comp-user-card,
.card {
  height: 254px;
  border-radius: 10px;
  background-color: $bg-color2;
  &__container {
    margin: 0 auto;
    @include user-card;   
  }
  &__wrapper,
  .user {
    max-width: inherit;
    padding: 20px;
    // border: 1px solid hotpink;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    text-align: center;
    position: relative;
    &__reg-date {
      position: absolute;
      @include body12;
      left: 10px;
      top: -15px;
    }
    &__photo {
      width: 70px;
      height: 70px;
      
      & > img {
        border-radius: 50%;
      }
    }
    &__name {
      max-width: inherit;
      position: relative;
      text-align: center;
      @include body16;
      @include txt-ellipsis;
    }
    &__mid-wrapper {
      max-width: inherit;
      text-align: center;
    }
    &__job {
      max-width: inherit;
      @include body16;
      @include txt-ellipsis;
    }

    &__email {
      max-width: inherit;
      @include body16;
      @include txt-ellipsis;
    }
    &__phone {
      @include body16;
      // @include txt-ellipsis;
    }
  }
}
.rel {
  position: relative;
  cursor: pointer;
}
.tooltip {
  position: absolute;
  bottom: -45px;
  border-radius: $brd-radius;
  @include body16;
  padding: 3px 16px;
  background-color: $black87;
  color: $bg-color2;
  z-index: 1;
  white-space: nowrap;
}

</style>
